import { SET_COMPLITEGOALS } from '../constants';

export default ( state = [], action ) => {
  switch( action.type ){

    case SET_COMPLITEGOALS: 
      const { compliteGoals } = action;
      return compliteGoals;
    default:
      return state;
  }

}


