import React, { Component } from 'react';
import { connect } from 'react-redux';


import { compliteGoalRef, goalRef } from '../../firebase';
import './GoalItem.css';


class GoalItem extends Component {

  compliteGoal = () => {
      const { email } = this.props.user;
      const { title, serverKey } = this.props.item;
      goalRef.child(serverKey).remove();
      compliteGoalRef.push({email, title})
  }
  render(){
    return (
      <li className="goalList__item">
        <div>
          <span className="goalList__email">{this.props.item.email } </span>- 
          <b className="goalList__title"> { this.props.item.title}</b>
        </div>
        <button 
          onClick={this.compliteGoal}
          className="btn btn-xs btn-info">
          Complite
        </button>
      </li>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state;
  return { user };
}

export default connect(mapStateToProps, null)(GoalItem);



