import { SIGNED_IN, SET_GOALS, SET_COMPLITEGOALS } from '../constants';


export function logUser(email) {
  const action = {
    type: SIGNED_IN,
    email
  }
  return action;
}


export function setGoals(goals) {
  const action = {
    type: SET_GOALS,
    goals
  }
  return action;
}



export function setCompliteGoals(compliteGoals) {
  const action = {
    type: SET_COMPLITEGOALS,
    compliteGoals
  }
  return action;
}





