import React, { Component } from 'react';
import { connect } from 'react-redux';


import { compliteGoalRef } from '../../firebase';
import './CompliteGoalList.css';
import { setCompliteGoals } from '../../actions';



class CompliteGoalList extends Component {
  componentDidMount(){
    compliteGoalRef.on('value', snap => {
      const compliteGoals = [];
      snap.forEach( goal => {
        const { email, title } = goal.val();
        const serverKey = goal.key;
        compliteGoals.push({ email, title, serverKey });
      })
      this.props.setCompliteGoals(compliteGoals)
    })
  }

  clearComplited = () => {
    compliteGoalRef.set([]);
  }

  render() {
    console.log(this.props.compliteGoals)
    return (
     <div>
      <ul className="compliteGoalList ">
        {this.props.compliteGoals.map( (item, index) => {
            return (
             <li className="compliteGoalList__item" key={item.serverKey}>
              <span className="compliteGoalList__email">{item.email } </span>- 
              <b className="compliteGoalList__title"> { item.title}</b>
             </li>
            )
          })
        }
      </ul>
      <button
        onClick={this.clearComplited} 
        className="btn btn-warning">
        Clear All
      </button>
     </div>
    );
  }
}

function mapStateToProps(state) {
  const { compliteGoals } = state;
  return {
    compliteGoals
  }
}

export default connect(mapStateToProps, { setCompliteGoals })(CompliteGoalList);



